/* Google Font Import--Poppins */
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;1,100;1,300&family=Montserrat:wght@400;700&family=Poppins:wght@200;500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}


:root {
  --bg-color: #111;
  --text-color: #fff;
  --secondry-color: #00844e;
}

section {
  max-width: 1400px;
  margin: 0 auto;
}

body {
  background: url('./assests/main-img.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
}

button {
  background: transparent;
  outline: none;
  border: none;
  color: var(--text-color);
}

.hidden {
  display: none;
}

a {
  color: var(--text-color);
  transition: all 0.5s;
}

a:hover {
  color: var(--secondry-color);
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.title .desc {
  opacity: 0.6;
}

.title .heading {
  font-size: 4rem;
}

.title .underline {
  width: 80%;
  height: 4px;
  position: relative;
  border-radius: 1rem;
  bottom: 0;
  left: 0;
  background-color: var(--secondry-color);
}

.title .underline::after {
  content: '';
  width: 3px;
  background-color: var(--bg-color);
  position: absolute;
  left: 0;
  height: 100%;
  animation: animate-underline 10s linear infinite;
}

.title .underline::before {
  content: '';
  width: 3px;
  background-color: var(--bg-color);
  position: absolute;
  left: 0;
  height: 100%;
  animation: animate-underline 10s linear infinite;
  animation-delay: 2s;
}

.container {
  width: 100%;
  background-color: var(--bg-color);
  min-height: 100vh;
}

@keyframes animate-underline {
  from {
    left: 0;
  }

  to {
    left: 100%;
  }
}

/* SCROLL BAR */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #313131;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--secondry-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--text-color);
}

@media screen and (max-width:768px) {
  .title .heading {
    font-size: 3rem;
  }
}

@media screen and (max-width:425px) {
  .title .heading {
    font-size: 2rem;
  }
}