.hero {
    width: 100%;
    height: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 15rem;

}

.hero h1 {
    font-size: 5rem;
}

.hero h2 {
    font-size: 2rem;
}

.bottom-social-icons {
    position: absolute;
    bottom: 0;
    right: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 2rem 0;
}

.bottom-social {
    font-size: 1.2rem;
    margin: 0.5rem 0rem;
    cursor: pointer;
    transition: all 0.5s;

}

.bottom-social:hover {
    color: var(--secondry-color);
}

/* RESPONSIVENESS */

@media screen and (max-width:768px) {
    .hero h1 {
        font-size: 3rem;
    }

    .hero h2 {
        font-size: 1.5rem;
    }
}