.about {
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about-container {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 2rem;
}

.about-left {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* background-color: #fff; */

}

.about-left .about-image {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.about-left .about-image img {
    width: 70%;

}

.about-right {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1.5rem;
}

.about-top {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 1.2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #313131;
}

.about-top p {
    color: var(--secondry-color);
}

.about-top h1 {
    font-size: 2.5rem;
}

.about-top .desc {
    color: var(--text-color);
    opacity: 0.6;
}

.bottom-up {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    padding-bottom: 1rem;
}

.bottom-up-detail {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;

}

.bottom-up-detail-head {
    font-style: italic;
}

.bottom-up-detail-desc {
    opacity: 0.6;
    font-size: 0.8rem;
}

.btn-cv {
    font-weight: 600;
    letter-spacing: .2rem;
}

@media screen and (max-width:768px) {
    .about-container {
        flex-direction: column;
    }

    .about-left .about-image {
        justify-content: center;
    }

    .about-right {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 1.5rem;
        padding: 0 1rem;
    }
}