.portfolio {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
    padding-top: 5rem;
    padding-bottom: 5rem;
}

.portfolio-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.portfolio-bar {
    margin-bottom: 1.5rem;
}

.bar-link {
    padding: 1rem;
    position: relative;
    cursor: pointer;

}

.bar-link::after {
    content: '';
    width: 0%;
    height: 3px;
    position: absolute;
    bottom: 0;
    left: 15%;
    background-color: var(--secondry-color);
    transition: all 0.5s;


}

.bar-link:hover::after {
    width: 70%;

}

.bar-link:hover {
    color: var(--secondry-color);

}

.bar-link:first-child {
    color: var(--secondry-color);
}

.bar-link:first-child::after {
    content: '';
    width: 70%;
    height: 3px;
    position: absolute;
    bottom: 0;
    left: 15%;
    background-color: var(--secondry-color);


}

.projects-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    width: 80%;
    margin-bottom: 5rem;
}

.project {
    width: 100%;
    position: relative;

}

.project .name {
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.project img {
    width: 100%;
    transition: all 0.5s;
}

.project img:hover {
    opacity: 0.4;
    cursor: pointer;
}

.project:hover .name {
    z-index: 1;
}

@media screen and (max-width:768px) {
    .projects-container {

        grid-template-columns: repeat(2, 1fr);

    }
}

@media screen and (max-width:425px) {
    .projects-container {

        grid-template-columns: repeat(1, 1fr);

    }
}