.contacts {
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.form-container {
    margin-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    /* gap: 1rem; */
}

.form {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 1.5rem;
}

.form .form-title {
    margin-bottom: 1rem;
}

.form .form-name {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.form input,
textarea {
    width: 100%;
    outline: none;
    background-color: #161616;
    border: none;
    padding: 1rem 2rem;
    font-family: 'poppins';
    color: #737373;
    border-bottom: 2px solid transparent;
}


textarea {
    resize: none;
}

.form input:hover {
    border-color: var(--secondry-color);
}

.contact-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: var(--text-color);
    background-color: var(--secondry-color);
    padding: 1rem;
    border-radius: 1.5rem;
    transition: all 0.5s;
}


.contact-btn:hover {
    cursor: pointer;
    color: var(--secondry-color);
    background-color: var(--text-color);
}

.info-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.info-container p {
    width: 60%;
}

.details-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.contact-details {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 3rem;
}

.detail-icon {
    font-size: 2rem;
    color: var(--secondry-color);

}

.detail {
    padding: 0 1rem;
    border-left: 2px solid #8080804d;
    padding-bottom: 1rem;
}

.detail h3 {
    color: var(--text-color);
}

.detail .desc {
    font-size: 0.8rem;
    opacity: 0.6;
}


@media screen and (max-width:1024px) {
    .form-container {
        align-items: center;
        flex-direction: column;
        gap: 4rem;
        margin-bottom: 2rem;
    }
}