.testimonials {

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.title {
    align-items: flex-start;
}

.testimonial-container {
    margin: 5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
}

.testimonial {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
    background-color: #161616;
    padding: 2rem;
}

.testimonial .top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.testimonial-icon {
    font-size: 3rem;
    color: var(--secondry-color);
}

.testimonial .top .left-image {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.testimonial .top .left-image .desc {
    opacity: .6;
    font-size: .9rem;
}

.testimonial .top .left-image .image {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.testimonial .top .left-image img {
    width: 100%;
}

.testimonial .bottom .desc {
    opacity: 0.6;
}

@media screen and (max-width:1024px) {
    .testimonial {
        width: 90%;
    }
}

@media screen and (max-width:768px) {
    .testimonial-icon {
        font-size: 2rem;
    }
}