.navbar {
    width: 100%;
    height: 5rem;
    padding: 1rem 3rem 0rem 3rem;
    /* background-color: red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo img {
    width: 100%;
    cursor: pointer;
}

.nav-links {
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-link {
    padding-left: 1.5rem;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.5s;
}

.nav-icon {
    font-size: 1.8rem;
    cursor: pointer;
    display: none;
    transition: all 0.5;

}

.nav-link:hover,
.nav-icon:hover {
    color: var(--secondry-color)
}


/* RESPONSIVENESS */

@media screen and (max-width:1600px) {
    .logo img {
        width: 70%;
    }
}

@media screen and (max-width:1024px) {
    .logo img {
        width: 80%;
    }

    .nav-links {
        display: none;
        position: absolute;
        flex-direction: column;
        top: 0rem;
        left: 0;
        width: 100vw;
        background-color: var(--bg-color);

    }

    .nav-link {
        padding: 1.5rem;
    }

    .nav-link:first-child {
        padding-top: 8rem;
    }

    .nav-icon {
        display: block;
    }

    .nav-icon-cross {
        position: absolute;
        top: 2rem;
        right: 3rem;
    }
}