.resume {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 4rem;
    background-color: var(--bg-color);
}

.degree-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex-wrap: wrap;
    margin: 5rem auto;

}



.degree-container .left .head,
.degree-container .right .head {
    font-size: 1.5rem;
    margin-bottom: 0.8rem;
}

.degree-container .left .education-container,
.degree-container .right .experience-container {
    border-left: 2px solid var(--secondry-color);
    display: flex;
    justify-content: center;
    gap: 1rem;
    background-color: #161616;

}

.degree-container .left .education-container .details,
.degree-container .right .experience-container .details {
    padding: 2rem 0;

}

.degree-container .left .education-container p,
.degree-container .right .experience-container p {
    margin-top: 1rem;
    font-stretch: semi-expanded;
    font-size: 0.8rem;
    opacity: 0.7;
}

.resume-icon {
    /* margin-top: 0.4rem; */
    margin-top: 2rem;
    color: var(--secondry-color);
    font-size: 1.8rem;
}

.skills-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 4rem;
    margin: 2rem auto;
}

.skill-container-box {
    width: 800px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;

}

.skill-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.skill-details {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.skill-bar {
    width: 100%;
    height: 10px;
    overflow: hidden;
    background-color: #2f2f2f;
    position: relative;
}

.skill-bar-progress {
    /* width: 95%; */
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--secondry-color);
}

.office-tour {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
    padding: 2rem;
}

.office-tour .details {
    width: 75%;
    text-align: center;
}

.office-tour h2 {
    margin-bottom: 1.5rem;
}

.office-tour p {

    opacity: 0.6;
    font-size: 0.9rem;
}

.youtube-video {
    display: flex;
    justify-content: center;
    align-items: center;

}


/* RESPONSIVENESS */
@media screen and (max-width:1024px) {
    .skill-container-box {
        width: 768px;

    }

    .office-tour .details {
        width: 80%;
        text-align: center;
    }

    .youtube-video {
        width: 540px;
        height: 310px;
    }


}


@media screen and (max-width:768px) {
    .skill-container-box {
        width: 400px;

    }

    .office-tour-video {
        width: 200px;
        height: 100px;
        /* aspect-ratio: 16/9; */
    }



    .office-tour .details {
        width: 90%;
        text-align: center;
    }

    .youtube-video {
        width: 300px;
        height: 145px;
    }
}

@media screen and (max-width:540px) {
    .youtube-video {
        width: 300px;
        height: 145px;
    }
}

@media screen and (max-width:425px) {
    .skill-container-box {
        width: 300px;

    }

    .office-tour .details {
        width: 100%;
        text-align: center;
    }
}

@media screen and (max-width:320px) {
    .skill-container-box {
        width: 250px;

    }
}

@media screen and (max-width:250px) {
    .skill-container-box {
        width: 230px;

    }
}

@media screen and (max-width:230px) {
    .skill-container-box {
        width: 200px;

    }
}