.blog-page {
    padding-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.blogs-container {
    margin-top: 5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    width: 80%;
}

.blog {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

.blog .image {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.blog .image .date {
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: var(--secondry-color);
    padding: 0 1rem;
}

.blog .image img {
    width: 100%;
    cursor: pointer;
}

.blog .name {
    margin-top: 1rem;
    font-size: 1.3rem;
    letter-spacing: 2px;
}

.blog .desc {
    margin-top: 0.5rem;
    opacity: .6;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px) {
    .blogs-container {

        grid-template-columns: repeat(2, 1fr);

    }
}

@media screen and (max-width:425px) {
    .blogs-container {

        grid-template-columns: repeat(1, 1fr);

    }
}